@import 'https://fonts.googleapis.com/css?family=Oswald';
@import 'https://fonts.googleapis.com/css?family=ABeeZee';
@import url('https://fonts.googleapis.com/css?family=Lobster');


$pfont:'ABeeZee',
sans-serif;
$font:'Oswald',sans-serif;
.mobileView {

    .text_head {
        display: flex;
        justify-content: center;
        padding: 1rem;
    }
    h3 {
        font-family: 'Lobster', cursive;
        text-align:center;
        font-size:30px;
        margin:20px 0px;
      }
      p {
        font-family:$pfont;
        text-align:center;
        margin:0px;
      }

      
      textarea.form-control {
        height: 500px !important;
    }
      .iphone {
        background:#000004;
        width: 290px;
        height: 550px;
        margin:auto;
        overflow:hidden;
        border-radius:30px;
      }
      .container {
        width: 270px;
        height: 435px;
        background: black;
        padding: 2px;
        display: flex;
        flex-direction: column;
        margin:30px auto;
        margin-bottom:20px;
      }
      
      
      .header {
        // height: 17%;
        width: 100%;
        background: #f9f9f9;
        position: relative;
      }
      
      .text {
        height: 83%;
        width: 100%;
        position: relative;
        background: #ffffff;
      }
      
      .not_bar {
        height: 15px;
        width: 100%;
        background: #f9f9f9;
        position: relative;
      }
      
      .profile {
        height: 40px;
        width: 40px;
        border-radius: 100px;
        font-size: 12px;
        text-align: center;
        margin: 10px auto;
        background: url(https://assets.entrepreneur.com/content/16x9/822/20150406145944-dos-donts-taking-perfect-linkedin-profile-picture-selfie-mobile-camera-2.jpeg);
        background-size: cover;
        line-height: 100px;
        font-family: $font;
      }
      
      .back {
        height: auto;
        position: absolute;
        width: 30px;
        font-size: 18px;
        font-family: $pfont;
        color: #319afc;
        left: 10px;
        // bottom: 8px;
      }
      
      .info {
        height: 20px;
        width: 20px;
        line-height: 20px;
        border: 2px solid #319afc;
        color: #319afc;
        text-align: center;
        border-radius: 100px;
        font-family: $font;
        position: absolute;
        right: 15px;
        bottom: 20px;
      }
      
      // notification bar
      .sim {
        height: 100%;
        width: 50px;
        font-family: $font;
        font-size: 11px;
        position: absolute;
        left: 5px;
        line-height: 17px;
      }
      
      .time {
        height: 100%;
        font-family: $font;
        font-size: 12px;
        text-align: center;
      }
      
      .wifi {
        height: 70%;
        width: 50px;
        background-color: red;
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 45px;
        margin: auto 1px;
        background: url(http://www.freeiconspng.com/uploads/3d-black-wifi-icon-9.png) no-repeat;
        background-size: contain;
      }
      
      .bat {
        height: 200%;
        width: 50px;
        background-color: red;
        position: absolute;
        top: 0px;
        bottom: 0px;
        right: -15px;
        margin: auto;
        background: url(http://gazettereview.com/wp-content/uploads/2016/01/energy-supplements.png) no-repeat;
        background-size: contain;
      }
      
      // tetx part
      .text {
        position: relative;
        height: 50px;
        .cam_icon {
          width: 80px;
          height: 50px;
          background: url(data:image/png;base64,) no-repeat center;
          background-size: 35px 35px;
        }
        .send_dock {
          position: absolute;
          ;
          bottom: 0px;
          width: 100%;
          height: auto;
          display: flex;
          background: #F9F9F9;
          #m_text {
            border-radius: 20px;
            border: 1px solid #ccc;
            height: 34px;
            width: 100%;
            margin: 0px;
            right: 0px;
            float: right;
            margin: 5px 5px;
            box-sizing: border-box;
            padding: 0px 10px;
            transition: all 0.3s ease-in-out;
            &:focus {
              outline: none;
            }
          }
        }
        .send_button {
          height: 100%;
          width: 100px;
          text-align: center;
          font-family: $pfont;
          line-height: 50px;
          text-decoration: bold;
          cursor: pointer;
        }
      }
      
      .m_input {
        height: auto;
        width: auto;
        position: realtive;
        display: inline-block;
        float: right;
      }
      
      .message_area {
          padding-top: 1rem;
        width: 100%;
        height: auto;
        max-height: 407px;
        background: #fff;
        flex: 1;
        display: block;
        overflow-Y: hidden;
        div {
          height: auto;
          width: 100%;
          display: block;
          overflow: hidden;
          .sms_box {
            height: auto;
            max-width: 80%;
            display: block;
            background: #3EB4FB;
            border-radius: 10px;
            padding: 10px;
            color: #fff;
            font-family: $pfont;
            font-size: 13px;
            margin: 3px 10px;
            float: right;
            :hover {
              background: #1ba8fc;
            }
          }
          .rep {
            float: left;
            background: #E7E6EC;
            color:#1C1B20;
          }
        }
      }
      
      .home {
        height:30px;
        width:30px;
        background:#000004;
        margin:-10px auto;
        border:1px solid #ccc;
        border-radius:100px;
        
      }
      
      .mic {
        width:100%;
        height:10px;
        display:flex;
        justify-content:center;
        margin-top:30px;
        
        li {
          display:block;
          background:#ccc;
          height:7px;
          float:left;
          border-radius:100px;
          margin:7px;
          
        }
      }
      
      .prox {
        width:8px;
        
      }
      .grid {
        width:70px;
      }
}
