  background: url(http://gazettereview.com/wp-content/uploads/2016/01/energy-supplements.png) no-repeat;
  background-size: contain; }

.mobileView .text {
  position: relative;
  height: 50px; }
  .mobileView .text .cam_icon {
    width: 80px;
    height: 50px;
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 35px 35px; }
  .mobileView .text .send_dock {
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: auto;
    display: flex;
    background: #F9F9F9; }
    .mobileView .text .send_dock #m_text {
      border-radius: 20px;
      border: 1px solid #ccc;
      height: 34px;
      width: 100%;
      margin: 0px;
      right: 0px;
      float: right;
      margin: 5px 5px;
      box-sizing: border-box;
      padding: 0px 10px;
      transition: all 0.3s ease-in-out; }
      .mobileView .text .send_dock #m_text:focus {
        outline: none; }
  .mobileView .text .send_button {
    height: 100%;
    width: 100px;
    text-align: center;
    font-family: "ABeeZee", sans-serif;
    line-height: 50px;
    -webkit-text-decoration: bold;
            text-decoration: bold;
    cursor: pointer; }

.mobileView .m_input {
  height: auto;
  width: auto;
  position: realtive;
  display: inline-block;
  float: right; }

.mobileView .message_area {
  padding-top: 1rem;
  width: 100%;
  height: auto;
  max-height: 407px;
  background: #fff;
  flex: 1 1;
  display: block;
  overflow-Y: hidden; }
  .mobileView .message_area div {
    height: auto;
