      font-family: "ABeeZee", sans-serif;
      font-size: 13px;
      margin: 3px 10px;
      float: right; }
      .mobileView .message_area div .sms_box :hover {
        background: #1ba8fc; }
    .mobileView .message_area div .rep {
      float: left;
      background: #E7E6EC;
      color: #1C1B20; }

.mobileView .home {
  height: 30px;
  width: 30px;
  background: #000004;
  margin: -10px auto;
  border: 1px solid #ccc;
  border-radius: 100px; }

.mobileView .mic {
  width: 100%;
  height: 10px;
  display: flex;
  justify-content: center;
  margin-top: 30px; }
  .mobileView .mic li {
    display: block;
